class Breadcrumbs {
    constructor(element) {
        // 
        this.$element = $(element);
        // 
        this.setListeners();
    }

    setListeners() {
        Capitalize.CapitalizeSting('faggot')
        let translateText = async (str, lang) => await translate(str, lang);
        const url = document.location.pathname;

        let filteredLinks = url.split('/').filter(el => el !== '');

        console.log(filteredLinks)

        if (filteredLinks.length >= 1) {
            this.$element.append(`/ <a href="/"></a> /`)
        }


        filteredLinks.forEach((item, index) => {
            let completeUrl = filteredLinks.slice(0, index + 1).join('/')
            translateText(filteredLinks[index], 'ru').then(response => {
                this.$element.append(`<a href="/${completeUrl}">${Capitalize.CapitalizeSting((response.split('%').join('')))}</a> /`)
            })
        })
    }
}

$(() => {
    $('.js-breadcrumb').each((i, item) => new Breadcrumbs(item));
});
